header,
.header {
    width: 100%;
    height: 44px;
    border-bottom: 1px solid #e1e1e1;
    .brand {
        width: 20%;
        font-size: 30px;
        margin-left: 10px;
        float: left;
    }
    .search {
        float: right;
        height: 30px;
        margin-right: 10px;
        width: 60%;
        .fa {
            position: absolute;
            margin-top: 10px;
            margin-left: 5px;
            z-index: 99;
        }
        input {
            margin-top: 2px;
            width: 100%;
            font-size: 20px;
            border: 1px solid #333;
            padding: 4px 0 4px 20px;
            box-sizing: border-box;
            background: transparent;
        }
    }
}
.top {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: #05a6b1;
    color: #fff;
    text-align: center;
    z-index: 9999;
    border: 0;
    .back {
        position: absolute;
        left: 10px;
        top: 50%;
        margin-top: -12px;
        font-size: 25px;
        color: #fff;
    }
    .btn {
        position: absolute;
        right: 10px;
        top: 50%;
        margin-top: -17px;
        border: 1px solid #fff;
        padding: 5px;
        color: #fff;
    }
    .title {
        line-height: 44px;
    }
}


footer,
.footer {
    width: 100%;
    height: 48px;
    background: #fff;
    border: 1px solid #e1e1e1;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    a,
    .item {
        width: 25%;
        text-align: center;
        display: block;
        float: left;
        padding-top: 4px;
        box-sizing: border-box;
        position: relative;
        font-size: .9rem;
        text-decoration: none;
        .tip {
            display: block;
            position: absolute;
            right: 30px;
            top: 0px;
            width: 16px;
            height: 16px;
            color: #fff;
            background: #e1454b;
            font-size: 10px;
        }
        &.active {
            color: #4693d0;
        }
        .fa {
            font-size: 1.5rem;
            display: block;
        }
    }
    &.comment {
        background: rgba(23,12,23,.7);
        a {
            width: 100%;
            text-align: center;
        }
    }
}